<template>
<div class="Cart">
    <!-- 头部 -->
    <div class="Cart_title">
        <span class="arrow-left" @click="back()"><van-icon name="arrow-left" size="2rem"/></span>
        <span style="font-size:25px">购物车</span>
        <span  @click="toggle" class="Cart_title2">{{con}}</span>
    </div>
    <!-- 占为 -->
    <div style="height:40px"></div>
    <!-- 购物车商品列表 -->
    <div class="Cart_product" v-for="(item,i) of list" :key="i">
        <van-checkbox style="padding:0 10px"  @change="changeItem" v-model="item.cb" ></van-checkbox>
        <van-card
            :num="item.count"
            :price="item.price"
            :title="item.lname"
            :thumb="'http://127.0.0.1:4000/'+item.pic"
            class="Cart-cart"
            >
            <div slot="footer">
                <van-stepper v-model="item.count" @plus="add(item.lid,item.lname,item.price,item.pic)" @minus="red(item.lid)"/>
            </div>
            <div slot="price-top" style="margin-left:90%">
                <van-icon name="delete" size="2rem" @click="del(item.id)"/>
            </div>
        </van-card>  
    </div>  
    <!-- 底部占为 -->
    <div style="height:50px"></div>
    <!-- 提交 删除 -->
    <van-submit-bar
        :price="totalPrice"
        button-text="提交订单"
        @submit="delAll"
        v-if="isA==false"
        >
         <div>全选 <input type="checkbox" v-model="allcb" @change="selectAll"></div>
    </van-submit-bar>
    <van-submit-bar
        :price="totalPrice"
        button-text="删除"
        @submit="delm"
        v-else
        >
        <div>全选 <input type="checkbox" v-model="allcb" @change="selectAll"></div>
    </van-submit-bar>
</div>
</template>
<script>
export default {
    data(){
        return{
            list:[],  //购物车存放列表
            value:0,
            isA:false, //定义一个变量点击切换
            con:"编辑",  // 头部右侧点击切换内容
            allcb:false, //全选状态
        }
    },
    created(){
        this.loadMore() 
    },
    methods:{
        back(){
            this.$router.back()
        },
        //增加商品 修改vuex全局变量
        add(lid,lname,price,pic){
            this.$store.commit("subCart")
            this.axios.get("/addcart",{
                    params:{
                        lid:lid,
                        lname,
                        price,
                        pic
                    }
                })
        },
        //增加商品 修改vuex全局变量
        red(lid){
            this.$store.commit("redCart")
            this.axios.get("/del1",{
                    params:{lid}
                })
        },
        //点击切换头部完成
        toggle(){
            this.isA=!this.isA;
            if(this.isA==true){
                this.con="完成"

            }else{
                this.con="编辑"
            }
        },
        //清除购物车
        delAll(){        
            this.allcb=true
            this.selectAll();
            this.delm();
        },
        //清除多个商品
        delm(){
          if(this.list.length==0){
              this.$toast("没有可删除商品")
              return;
          } 
          let str="";
          for(let item of this.list){
              if(item.cb==true){
                 str+=item.id+","
              }
          }
          if(str.length==0){
              this.$toast("请选择要删除的商品")
              return;
          }else{
              str = str.substring(0,str.length-1);
              this.$dialog.confirm({
                   title: '删除',
                message:"删除此商品"
              }).then((res)=>{
                  this.axios.get("delm",{
                      params:{
                         id:str
                      }
                  }).then((res)=>{
                      this.loadMore();
                      this.$toast("删除成功")
                  })
              })
              .catch((err)=>{})
          }
        },
        selectAll(){
            //功能:全选按钮状态修改操作
            let cb1 = this.allcb;
            for(let item of this.list){
                item.cb = cb1
            }
        },
        //商品单选按钮状态
        changeItem(){
            let sum = 0;
            for(let item of this.list){
                if(item.cb)sum++;
            }
            if(this.list.length==sum){
                this.allcb=true;
            }else{
                this.allcb=false
            }
        },
        //删除一个商品
        del(id){
            this.$dialog.confirm({
                title: '删除',
                message:"删除此商品"
            }).then((res)=>{
                this.axios.get("/del",{
                    params:{id}
                })
                .then((res)=>{
                    if(res.data.code==-1){
                        this.$toast("删除失败")
                    }else{
                        this.$toast("删除成功");
                        this.loadMore();
                    }     
                })
            })
             .catch((err)=>{
            })
        },
        //加载购物车页面函数
        loadMore(){
            this.axios.get("/findcart")
            .then(res=>{
                if(res.data.data==-2){
                    this.$toast("请登录")
                    this.$router.push("/Login")
                }else{
                    let rows =res.data.data
                    this.list=rows;
                    let sum = 0;
                    for( let item of rows ){
                        sum+=item.count
                    }
                    this.$store.commit("addmCart",sum)
                }
            })
        },

    },
    computed:{
        totalPrice(){
            let totalPrice=0
             for(let item of this.list){
                 if(item.cb==true){
                     totalPrice+=item.count*item.price*100
                 }
            }
            return totalPrice
        }
    },
    //     beforeRouteLeave(to,from,next){
    //   if(to.name=='Products'){
    //     to.meta.keepAlive=true
    //   }
    //   console.log('从想拼')
    //   next()
    // }
}
</script>
<style scoped>
.arrow-left{
    position: fixed;
    top:0.5rem;
    left: 0.3rem;
}
.van-submit-bar{
    bottom: 50px
}
.Cart{
    position: relative;
}
.Cart_title{
    position: fixed;
    width: 100%;
    height:40px;
    z-index: 99;
    background-color:#fff;
    line-height: 40px;
   text-align: center; 
}
.Cart_title span:last-child{
    color: red
}
.Cart_title2{
   float: right;
}
.Cart_product{
    width: 100%;
    display: flex;
    
}
.Cart-cart{
    width: 100%;
}
</style>