<template>
    <div class="Click_top" v-show="top" @click="Click_top">
       <van-icon name="upgrade" size="3rem" color="#ccc"/>
   </div>
</template>
<script>
export default {
    data(){
        return{
            top:false
        }
    },
    methods:{
       Click_top(){
          const timer = requestAnimationFrame(function fn(){
                var oTop = window.pageYOffset ||document.body.scrollTop || document.documentElement.scrollTop;
                if(oTop > 0){
                    scrollTo(0,oTop-50);
                    requestAnimationFrame(fn);
                }else{
                    cancelAnimationFrame(timer);
                } 
            });
        },
        handleScroll () {
            const scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            if(scrollTop>=80){
               this.top=true
            }else{
               this.top=false
            }
            
        },
    },
    mounted(){
        window.addEventListener('scroll', this.handleScroll)
    }
 }

</script>
<style  scoped>
.Click_top{
    background:#fff;
    border: solid 1px #ccc;
    width:3rem;
    height:3rem;
    border-radius:3px;   
    position: fixed;
    bottom: 5rem;
    right: 1rem;
    opacity: 0.8;
}
</style>