<template>
<div id="app">
    <!-- 头部搜索 -->
    <div class="searchTitle">     
       <van-icon name="arrow-left" size="30" @click="history()"/>
        <form action="/" style="width:100%">
            <van-search
                v-model="value"
                show-action
                shape="round"
                placeholder="请输入搜索关键词"
                @search="onSearch">
                <div slot="action" @click="onSearch">搜索</div>
            </van-search>
        </form>
    </div>
    <!-- 实时监听 -->
    <div class="searchcontent" v-show="value">
        <ul>
            <li class="search-item" v-for="(item,i) of searchlist" :key="i" @click="goSearch(item.lid)">{{item.title}}</li>
            <li class="search-item"  v-show="hasNoData">没有找到匹配数据</li>
        </ul>
    </div>
    <!-- 搜索历史 -->
    <div class="searchHistory">
       <span>搜索历史</span> 
       <van-icon name="close" @click="cleardata()" size="2rem"/>    
    </div>
    <div v-for="(item,i) of historyWord" :key="i" class="historysearch">
        <button @click="historySearch(item)">{{item}}</button>
        <!-- <van-tag round type="success" size="3rem" @click="historySearch()">{{item}}</van-tag> -->
        <!-- {{item}} -->
    </div>
</div>
</template>
<script>
export default {
    data(){
        return{
            value:"",
            list:[],
            searchlist:[],
            timer:null,
            historyWord:""
        }
    },
    methods:{
        history(){
            this.$router.go(-1)
        },
        loadMore(){
            this.historyWord = localStorage.getItem("searchWord")
            if(this.historyWord !=null){this.historyWord = this.historyWord.split(',')
        }},   
        onSearch(){
            //点击搜索商品
            console.log(this.value)
            if(this.value==""){
                this.$toast("请输入商品名称")
            return
            }
            let all=[]
            this.axios.get("/allproduct")
            .then(res=>{
                all=res.data.data
                 let value =this.value
             this.list = all.filter(elem=>{    
             return elem.title.indexOf(value) !== -1})
              //如果搜索成功，添加搜索历史记录
             if(this.list.length!==0){
                 let searchWord=localStorage.getItem('searchWord');           
                if(searchWord==null){
                    localStorage.setItem('searchWord',this.value);          
                }else{
                    let arr=localStorage.getItem('searchWord')
                    arr=arr.split(',')
                    if(arr.indexOf(this.value)!==-1){
                        arr=arr
                    }else{
                        arr.unshift(this.value);
                    }
                    this.historyWord=arr
                    // console.log(this.historyWord)
                    if(this.historyWord.length>4){
                        this.historyWord.pop()
                    }
                    localStorage.setItem('searchWord',this.historyWord)
                }
             }
             
            })
            //跳转商品页
            let value =this.value
             this.$router.push({
                 path:"/Products",
                   query:{
                    value
                  }
             })
            
        },
        goSearch(){
            this.onSearch()
        },
        historySearch(value){
             this.$router.push({
                 path:"/Products",
                   query:{
                    value
                  }
             })
        },
        cleardata(){
            localStorage.removeItem("searchWord")
            this.historyWord=[]
        }

    },
//监听值的变化触发搜索引擎
    watch:{
        value () {
            if (this.timer) {
                clearTimeout(this.timer)
            }
            if (!this.value) {
                this.list = []
                return
            }
            this.timer = setTimeout(() => {
                var all=[]
                this.axios.get("/allproduct")
                .then(res=>{
                all=res.data.data
                var value =this.value
                this.searchlist = all.filter(elem=>{    
                    return elem.title.indexOf(value) !== -1}
                    )
                })
            }, 1000)
        }
    },
 //判断  没有找到数据  得显示隐藏
    computed: {
        hasNoData () {
        return !this.searchlist.length
        }
    },
    //搜索历史
    created(){
        this.loadMore()
    }
}
</script>
<style scoped>
#app{
    position: relative;
}
.searchTitle{
    display: flex;
    align-items: center;
    border-bottom: solid #ccc 1px;
}
.searchHistory{
    display: flex;
    justify-content: space-between;

    
}
.searchcontent{
    z-index: 1;
    position: absolute;
    top: 4rem;
    left: 2rem;
    right: 2rem;
    bottom: 0;
    background: red
 }
 .search-item{
    line-height: 2rem;
    padding-left: .2rem;
    background:red;
    color: #666      
}
.historysearch{
    width: 100%;
    display: flex;
    flex-direction: row;
}
</style>