<template>
<div>
  <van-field
    v-model="username"
    name="用户名"
    label="用户名"
    placeholder="用户名"
    :rules="[{ required: true, message: '请填写用户名' }]"
  />
  <van-field
    v-model="password"
    type="password"
    name="密码"
    label="密码"
    placeholder="密码"
    :rules="[{ required: true, message: '请填写密码' }]"
  />
  <div style="margin-top:2rem">
    <van-button round block type="info" @click="login" >
      登录
    </van-button>
    <van-button round block type="info" @click="register" style="margin-top:1rem">
      注册
    </van-button>
  </div>
</div>

</template>
<script>
export default {
    data() {
      return {
        username: '',
        password: '',
      }
    },
    methods: {
      login() {
        const reg = /^[a-z0-9]{3,12}$/i
        const u = this.username;
        const p = this.password;
        if(!reg.test(u)){
          this.$dialog.alert({
            message: '用户名格式不正确'
          })
          return;
        }
        if(!reg.test(p)){
            this.$dialog.alert({
              message:"密码格式不正确"
            });
            return;//停止程序运行
        } 
        this.axios.get("/Login",{
          params:{
            uname:u,
            upwd:p
          }
        })
        .then(res=>{
          if(res.data.code==-1){
            this.$dialog.alert({
              message:'用户名或者密码有误'
            })
          }else{
            this.$toast("登录成功");
            this.$router.back()
          }
        }) 
      },
      register(){
        const reg = /^[a-z0-9]{3,12}$/i
        const u = this.username;
        const p = this.password;
        if(!reg.test(u)){
          this.$dialog.alert({
            message: '用户名格式不正确'
          })
          return;
        }
        if(!reg.test(p)){
            this.$dialog.alert({
              message:"密码格式不正确"
            });
            return;//停止程序运行
        } 
        this.axios.get("/Register",{
          params:{
            uname:u,
            upwd:p
          }
        })
        .then(res=>{
          if(res.data.code==1){
            this.$dialog.alert({
              message:'注册成功'
            })
          }else{
            this.$toast("注册失败");
          }
        }) 
      }
    },
  }
</script>
