    created(){
    this.hotproduct1()
    },
    methods:{
      onSearch(){
        this.$router.push("/Search")
      },
      routerProducts(family_name){
        this.$router.push({
          path:"Products",
          query:{
            family_name
          }
        })
      },
      hotproduct1(){
          this.axios.get("/allproduct").then(res=>{
          this.hotproduct=res.data.data
        })
      },
      routerDetail(lid){
                this.$router.push({
                  path:"/Detail",
                    query:{
                        lid:lid
                    }
                })
            },
      addcart(lid,lname,price,pic){
          this.$store.commit("subCart")
          this.axios.get("/addcart",{
              params:{
                  lid:lid,
                  lname,
                  price,
                  pic
              }
          })
          .then(res=>{
              if(res.data.code==-2){
                  this.$toast("请登录")
                  this.$router.push("/Login")
              }else
              {
                  this.$toast("添加成功")
              }
          })
      }
    },
