<template>
  <div class="Detail">
    <!-- 顶部图片／或者放轮播图 -->
    <div >
      <van-image :src="pic2" ></van-image>
    </div>
    <div class="arrow-left" @click="back()">
      <van-icon name="arrow-left" size="2rem"/>
    </div>
    <h2>{{product.title}}</h2>
    <h4>{{product.descript}}</h4>
    <p style="color:red;font-size:1.5rem">¥{{product.price}}</p>
    <van-divider />
    <p>
      <van-icon name="warning-o" />不支持7天无理由退货
    </p>
    <p>
      <span style="padding-right:1rem">规格:</span>
      <van-tag plain type="success" size="medium">12kg/箱</van-tag> 
    </p>
    <van-divider />
    <p>送至</p>
    <van-divider />
    <p>暂无评价</p>
    <van-divider />
    <div style="height:50px"></div>
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="客服" />
      <van-goods-action-icon icon="cart-o" text="购物车" @click="routerCart" :info="$store.getters.getCartCount" />
      <van-goods-action-button type="warning" text="加入购物车" @click="addcart(product.lid,product.title,product.price,product.pic)" />
      <van-goods-action-button type="danger" text="立即购买"/>
    </van-goods-action>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value:0,
      product:{}
    }
  },
  methods:{
    back(){
      this.$router.go(-1)
    },
    addcart(lid,lname,price,pic){
      this.$store.commit("subCart")
      this.axios.get("/addcart",{
        params:{
            lid,
            lname,
            price,
            pic
        }
      })
      .then(res=>{
        if(res.data.code==-2){
          this.$toast("请登录")
          this.$router.push("/Login")
        }else{
          this.$toast("添加成功")
        }
      })
    },
    //跳转到购物车
    routerCart(){
      this.$router.push("/Cart")
    },
    //加载页面
    loadMore2(){
      let lid=this.$route.query.lid
      this.axios.get("/detail",{
          params:{
            lid
          }
      })
      .then(res=>{
        this.product=res.data.data[0];
        console.log(res.data.data[0])
      })
    }
  },
  created(){
    this.loadMore2()
  },
  computed:{
    pic2(){
      return this.axios.defaults.baseURL + '/' +this.product.pic
    }
  }
}
</script>
<style scoped>
.Detail{
  margin-left: 1rem;
}
.arrow-left{
    position: fixed;
    top: 1rem;
    left: 1rem;
}
</style>