<template>
<div id="app">
    <div class="arrow-left" @click="back()"><van-icon name="arrow-left" size="2rem"/></div>
    <van-tabs v-model="active" @click="compare1()">
        <van-tab title="销量" > 
            <!-- <div class="noone" v-if="this.list.length==0">没有查找到相应商品</div> -->
            <van-card
                :price="product.price"
                :desc="product.descript"
                :title="product.title"
                :thumb="'http://127.0.0.1:4000/'+product.pic"
                v-for="(product,i) of list" :key="i"
                @click-thumb="routerDetail(product.lid)"
                
                >
                <div slot="tags">
                    <van-tag plain type="danger" color="#4fc08d">直降10元</van-tag>
                </div>
                <div slot="num">
                <van-icon name="add-o" color="#4fc08d" size="2rem" @click="addcart(product.lid,product.title,product.price,product.pic)"/>
                </div>
            </van-card>          
        </van-tab>
        <van-tab title="价格" >
            <div class="noone" v-if="this.list.length==0">没有查找到相应商品</div>
            <van-card
                :price="product.price"
                :desc="product.descript"
                :title="product.title"
                :thumb="'http://127.0.0.1:4000/'+product.pic"
                v-for="(product,i) of list" :key="i"
                @click="routerDetail(product.lid)"
                v-else
                >
                <div slot="tags">
                    <van-tag plain type="danger" color="#4fc08d">直降10元</van-tag>
                </div>
                <div slot="num">
                <van-icon name="add-o" color="#4fc08d" size="2rem" @click="addcart(product.lid,product.title,product.price,product.pic)"/>
                </div>
            </van-card>  
        </van-tab>
        <van-tab title="新品">内容 3</van-tab>
    </van-tabs>
    <!-- 定位购物车标 -->
    <div  class="Products_car" @click="Products_car">
        <van-icon name="shopping-cart-o" :info="$store.getters.getCartCount" size="3rem" />
   </div>
   <!-- 定位点击到顶 -->
   <click-top></click-top>
</div>
</template>
<script>
import clickTop from '../components/Click_top.vue'
    export default {
        components:{
            clickTop
        },
        data(){
            return{
                active:"销量",
                al:false,
                list:[],//商品列表
                pno:0,//页码
                fname:"",
                isFirstEnter:""
            }
        },
        methods:{
            back(){
                // this.$destroy(true) 
                this.$router.go(-1)
            },
            compare1(){
                // console.log(1111)
                let arr=this.list
                function compare(prop){
                    return function(a,b){
                        let value1=a[prop]
                        let value2=b[prop]
                        return value1 - value2
                    }
                }
                arr.sort(compare("price"))
                this.list=arr
                // console.log(arr)
            },
            routerDetail(lid){
                this.$router.push({
                    path:"/Detail",
                    query:{
                        lid
                    }
                })
            },
            loadMore(){          
                let fname=
                this.$route.query.family_name;
                if(fname==null){
                    return
                }
                console.log(fname)
                this.pno++;
                this.axios.get("/Products",{
                    params:{
                        family_name:fname,
                        pno:this.pno,
                        pageSize:5              
                    }
                })
                .then(res=>{
                    const rows = this.list.concat(res.data.data)
                    this.list = rows;
                    console.log(res.data.data)
                })
            },
            loadMore1(){
                let value = this.$route.params.value
                if(value==null){
                    return
                }
                let all=[]
                this.axios.get("/allproduct")
                .then(res=>{
                    all=res.data.data
                    this.list = all.filter(elem=>{    
                    return elem.title.indexOf(value) !== -1})
                    // console.log(this.list)
                })
            },
            Products_car(){
                this.$router.push('/Cart')
            },
            addcart(lid,lname,price,pic){
                this.$store.commit("subCart")
                this.axios.get("/addcart",{
                    params:{
                        lid:lid,
                        lname,
                        price,
                        pic
                    }
                })
                .then(res=>{
                    if(res.data.code==-2){
                        this.$toast("请登录")
                        this.$router.push("/Login")
                    }else
                    {
                        this.$toast("添加成功")
                    }
                })
            }
        },
        created(){
            this.loadMore()
            this.loadMore1()
          
                 
        },
        //页面缓存
        // beforeRouteLeave(to,from,next){
        //     if(to.name =='Cart'){
        //         from.meta.keepAlive=true
        //     }
        //     next()
        // },
        // beforeRouteEnter(to,from,next){
        //     if(from.name="Cart"){
        //         to.meta.isBack=true
        //     }
        //     next()
        // },
        // beforeRouteLeave(to,from,next){
        //     if(to.name =='Detail'){
        //         to.meta.keepAlive=true
        //     }
        //     next()
        // }
        // activated(){
        //     if(!this.$route.meta.isBack){  
        //         // this.routerDetail()  
                
        //         this.loadMore()
                
        //     }     
        //     this.$route.meta.isBack = false;
        // }
       
    }
</script>
